async function getStorageValue(key) {
  const val = window.localStorage.getItem(key);
  return val;
}

async function setStorageValue(key, val) {
  window.localStorage.setItem(key, val);
}

async function clearStorageValue() {
  window.localStorage.clear();
}

function openUrlHere(url) {
  const win = window.open(url, '_self');
  win.focus();
}

function openUrl(url) {
  const win = window.open(url, '_blank');
  win.focus();
}

function remainingText(seconds) {
  let secs = seconds;
  let numDays = Math.floor(secs / (3600 * 24));
  secs = secs - (numDays * 3600 * 24);
  let numHours = Math.floor(secs / 3600);
  secs = secs - (numHours * 3600);
  let numMinute = Math.floor(secs / 60);
  secs = secs - (numMinute * 60);
  let numSeconds = secs;
  let ret = '';
  if (numDays > 0) ret += (numDays + 'd ');
  if (numHours > 0) ret += (numHours + 'h ');
  if (numMinute > 0) ret += (numMinute + 'm ');
  if (numSeconds > 0) ret += (numSeconds + 's');
  return ret;
}

function to4dec(amount) {
  return (Math.round(Number(amount) * 10000) / 10000);
}

function to2dec(amount) {
  return (Math.round(Number(amount) * 100) / 100);
}

export default {
  delay,
  openUrl,
  openUrlHere,
  showToast,
  getStorageValue,
  setStorageValue,
  clearStorageValue,
  remainingText,
  to4dec,
  to2dec
};

