import React from 'react';
import { View, Dimensions } from 'react-native';
import { ThemeProvider, Button } from 'react-native-elements';
// import Landing from './game/Landing';
import Home from './game/Home';
import {
  MemoryRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import log from 'loglevel';

log.setLevel('debug');

const theme = {
  colors: {
    primary: 'darkblue'
  }
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: 'busy',
      landscape: false,
      w: Dimensions.get('window').width
    };
  }

  componentDidMount() {
  }

  componentWillMount() {
  }

  render() {
    let ww = Dimensions.get('window').width;
    let wh = Dimensions.get('window').height;
    let w = ww - 20;
    let h = wh;

    return (
      <>
        <style type="text/css">{`
          @font-face {
            font-family: 'FontAwesome';
            src: url(${require('react-native-vector-icons/Fonts/FontAwesome.ttf')}) format('truetype');
          }
          @font-face {
            font-family: 'EvilIcons';
            src: url(${require('react-native-vector-icons/Fonts/EvilIcons.ttf')}) format('truetype');
          }
        `}</style>

        <View style={{ minWidth: w, height: h }}>
          <ThemeProvider theme={theme}>

            <Router>
              <Switch>
                <Route path="/" component={Home} />
              </Switch>
            </Router>
          </ThemeProvider>
        </View>
      </>
    );
  }

}

export default App;
