function to4dec(amount) {
  return (Math.round(Number(amount) * 10000) / 10000);
}

function to2dec(amount) {
  return (Math.round(Number(amount) * 100) / 100);
}

class ModalTx extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  render() {
    // <ModalTx modalVisible={this.state.modalTxVisible} txHash={this.state.txHash} txError={this.state.txError} onClose={()=>this.setState({modalTxVisible:false})} />
    let indicator = <ActivityIndicator color='gray' size='large' />;
    let msg = 'PROCESSING TX';
    let btnClose = <Button type="outline" title='CLOSE' onPress={() => this.props.onClose()} />;
    if (this.props.txHash) {
      msg = 'PROCESS TX DONE';
      const txHash = this.props.txHash;
      indicator = <Button type="outline" title='OPEN ON EXPLORER' onPress={() => Lib.openUrl(cmfLib.EURL + '/tx/' + txHash)} />
    } else if (this.props.txError) {
      msg = 'PROCESS TX FAIL';
      indicator = <Text style={{ textAlign: 'center', color: 'red' }}>{this.props.txError}</Text>
    } else {
      btnClose = null;
    }

    return (
      <Modal
        isVisible={this.props.modalVisible}
        onBackdropPress={() => this.props.onClose()}
      >
        <View style={{ alignItems: 'center', justifyContent: 'center' }}>
          <View style={{ backgroundColor: 'white', padding: 2 * vp, width: 300 }}>
            <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
              <Text> </Text>
              <Text style={{ textAlign: 'center' }}>{msg}</Text>
              <Text> </Text>
            </View>
            <View>
              {indicator}
              <View style={{ height: vp }} />
              {btnClose}
            </View>
          </View>
        </View>
      </Modal>
    );
  }

}

export default ModalTx;
