import React from 'react';
import { View, Text, ScrollView, ActivityIndicator, Image, TextInput, CheckBox, FlatList } from 'react-native';
import { Button } from 'react-native-elements';
import screen from '../screen';
import Icon from 'react-native-vector-icons/FontAwesome';
import Lib from '../Lib';
import cmfLib from '../cmfLib';
import _ from 'lodash';
import moment from 'moment';
import Modal from 'modal-enhanced-react-native-web';
import log from 'loglevel';
import ModalTx from './ModalTx';

const vp = screen.vp;
const vps = screen.vps;
const landscape = screen.landscape;
const to4dec = Lib.to4dec;
const to2dec = Lib.to2dec;

class Farm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  async componentDidMount() {
    // this.interval = setInterval(() => {
    //   this.setState({ ts: moment().unix() });
    // }, 1000);
    const i = this.props.initial;
    if (i) {
      this.setState({
        gd: i.gd,
        ud: i.ud
      });
    } else {
      await this.refreshData();
    }
  }

  componentWillUnmount() {
    // clearInterval(this.interval);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.userAddress !== this.props.userAddress) {
      this.refreshData();
    }
  }

  async refreshData() {
    const { globalDataFunc, userDataFunc } = this.props.config;
    try {
      const userAddress = this.props.userAddress;
      const gd = await globalDataFunc();
      const ud = await userDataFunc(userAddress);
      this.props.onRefreshData();
      this.setState({ gd, ud });
    } catch (err) {
      console.error(err);
    }
  }

  async doApprove() {
    const { approveFunc } = this.props.config;
    this.setState({ modalTxVisible: true, txHash: null, txError: null });
    try {
      const txHash = await approveFunc();
      await this.refreshData();
      this.setState({ txHash: txHash });
    } catch (err) {
      log.error(err);
      const errMsg = err && err.data && err.data.message ? err.data.message : 'unknown error';
      this.setState({ txError: errMsg });
    }
  }

  async doStake(amount) {
    const { stakeFunc } = this.props.config;
    this.setState({ modalTxVisible: true, txHash: null, txError: null });
    try {
      const txHash = await stakeFunc(amount);
      await this.refreshData();
      this.setState({ txHash: txHash });
    } catch (err) {
      log.error(err);
      const errMsg = err && err.data && err.data.message ? err.data.message : 'unknown error';
      this.setState({ txError: errMsg });
    }
  }

  async doUpdate() {
    const { updateFunc } = this.props.config;
    this.setState({ modalTxVisible: true, txHash: null, txError: null });
    try {
      const txHash = await updateFunc();
      await this.refreshData();
      this.setState({ txHash: txHash });
    } catch (err) {
      log.error(err);
      const errMsg = err && err.data && err.data.message ? err.data.message : 'unknown error';
      this.setState({ txError: errMsg });
    }
  }

  async doClaim() {
    const { claimFunc } = this.props.config;
    this.setState({ modalTxVisible: true, txHash: null, txError: null });
    try {
      const txHash = await claimFunc();
      await this.refreshData();
      this.setState({ txHash: txHash });
    } catch (err) {
      log.error(err);
      const errMsg = err && err.data && err.data.message ? err.data.message : 'unknown error';
      this.setState({ txError: errMsg });
    }
  }

  async doUnstake(amount) {
    const { unstakeFunc } = this.props.config;
    this.setState({ modalTxVisible: true, txHash: null, txError: null });
    try {
      const txHash = await unstakeFunc(amount);
      await this.refreshData();
      this.setState({ txHash: txHash });
    } catch (err) {
      log.error(err);
      const errMsg = err && err.data && err.data.message ? err.data.message : 'unknown error';
      this.setState({ txError: errMsg });
    }
  }

  async doWithdraw() {
    const { withdrawFunc } = this.props.config;
    this.setState({ modalTxVisible: true, txHash: null, txError: null });
    try {
      const txHash = await withdrawFunc();
      await this.refreshData();
      this.setState({ txHash: txHash });
    } catch (err) {
      log.error(err);
      const errMsg = err && err.data && err.data.message ? err.data.message : 'unknown error';
      this.setState({ txError: errMsg });
    }
  }

  render() {
    const { stakeToken, rewardToken } = this.props.config;

    const userAddress = this.props.userAddress;
    const gd = this.state.gd;
    const ud = this.state.ud;
    const connected = (gd && ud);

    const s1 = { textAlign: 'center', fontWeight: 'bold', textTransform: 'uppercase', fontSize: 24 };
    const s2 = { textAlign: 'center', textTransform: 'uppercase' };

    let ts = moment().unix();
    let apr = 0;
    let apy = 0;
    let dailyPctg = apr / 365;
    let owned = '0.0';
    let staked = '0.0';
    let reward = '0.0';
    let pendingWithdraw = '0.0';
    let unstakeTs = moment().unix();
    let claimTs = moment().unix();
    let withdrawTs = moment().unix();
    let approved = '0.0';

    if (gd) {
      ts = gd.ts;
      apr = gd.apr;
      dailyPctg = apr / 365;
      apy = 100 * (Math.pow(1 + (dailyPctg / 100), 365) - 1);
      apy = to2dec(apy);
    }

    if (ud) {
      owned = ud.owned;
      staked = ud.staked;
      reward = ud.reward;
      pendingWithdraw = ud.pendingWithdraw;
      unstakeTs = ud.unstakeTimestamp;
      claimTs = ud.claimTimestamp;
      withdrawTs = ud.withdrawTimestamp;
      approved = ud.approved;
    }

    const unstakeTime = moment.unix(unstakeTs).format('DD MMM HH:mm');
    const claimTime = moment.unix(claimTs).format('DD MMM HH:mm');
    const withdrawTime = moment.unix(withdrawTs).format('DD MMM HH:mm');
    const needApprove = (Number(approved) < Number(owned));

    let btnApprove = null;
    let unstakeTimeWarning;
    let btnClaimReward = null;
    let enableStake = false;
    let enableUnstake = false;
    let enableClaim = false;
    let enableWithdraw = false;
    let title = 'Stake ' + stakeToken + ' earn ' + rewardToken;
    let description = 'Avg. daily reward is ' + to2dec(dailyPctg) + '%';
    let msgReward;
    let btnUpdate = null;

    enableClaim = (Number(reward) > 0);
    enableStake = (Number(owned) > 0 && !enableClaim && !needApprove);
    enableUnstake = (Number(staked) > 0 && ts > unstakeTs && !enableClaim);
    enableWithdraw = (Number(pendingWithdraw) > 0 && ts > withdrawTs);

    if (needApprove) {
      btnApprove = <View style={{ paddingBottom: vps(1) }}><Button title={'Approve'} onPress={() => this.doApprove()} /></View>;
    }

    if (ts <= unstakeTs && Number(staked) > 0) {
      unstakeTimeWarning = <Text>Unstake available after {unstakeTime}</Text>;
    }

    if (enableClaim)
      msgReward = 'Reward ' + reward + ' ' + rewardToken;
    else {
      if (claimTs <= ts)
        msgReward = 'Reward available soon..';
      else
        msgReward = 'Reward available after ' + claimTime;
    }

    btnClaimReward = <Button disabled={!enableClaim} title={'Claim Reward'} onPress={() => this.doClaim()} />;

    if (Number(pendingWithdraw) > 0) {
      if (enableWithdraw)
        msgReward = 'Unstaked ' + pendingWithdraw + ' ' + stakeToken + '';
      else
        msgReward = 'Unstaked ' + pendingWithdraw + ' ' + stakeToken + '\nWithdraw available after ' + withdrawTime;
      btnClaimReward = <Button disabled={!enableWithdraw} title={'Withdraw'} onPress={() => this.doWithdraw()} />;
    }

    let sPanel = { borderWidth: 1, borderColor: 'gainsboro', backgroundColor: 'white', padding: vps(2) };
    if (!landscape) sPanel.width = vps(90);

    const btnBack = (
      <View style={{ paddingTop: vps(1) }}>
        <Button type='outline' title={'Back'} onPress={() => this.props.onBack()} />
      </View>
    );

    return (
      <View style={{
        flex: 1, alignItems: 'center', justifyContent: 'center',
        backgroundColor: 'white',
        paddingVertical: vps(5)
      }}>
        <ModalTx modalVisible={this.state.modalTxVisible} txHash={this.state.txHash} txError={this.state.txError}
          onClose={() => this.setState({ modalTxVisible: false })} />
        <View style={sPanel}>
          <Text> </Text>
          <Text style={s2}>{title}</Text>
          <Text style={s1}>{apy}% APY</Text>
          <Text style={s2}>{description}</Text>
          <Text> </Text>
          <Text> </Text>
          <Text> </Text>
          <Text>Owned {owned} {stakeToken}</Text>
          <Text> </Text>
          <View style={{ flexDirection: 'row' }}>
            <View style={{ flex: 1 }}>
              <TextInput
                value={this.state.amountInput}
                onChangeText={txt => this.setState({ amountInput: txt })}
                keyboardType='numeric'
                placeholder={'Amount stake/unstake'}
                style={{ borderWidth: 1, padding: 5, flex: 1 }}
              />
            </View>
            <View style={{ width: vp }} />
            <View style={{ flexDirection: 'row', alignItems: 'center' }}>
              <Button disabled={true} title={stakeToken} />
            </View>
          </View>
          <Text> </Text>
          <Text>Staked {staked} {stakeToken}</Text>
          {unstakeTimeWarning}
          <Text> </Text>
          {btnApprove}
          <View style={{ flexDirection: 'row' }}>
            <Button style={{}} type='outline' title='100%' onPress={
              () => this.setState({ amountInput: owned })
            } />
            <View style={{ width: vp }} />
            <View style={{ flex: 1 }}>
              <Button disabled={!enableStake} title='Stake' onPress={
                () => this.doStake(this.state.amountInput)} />
            </View>
          </View>
          <View style={{ height: vp }} />
          <View style={{ flexDirection: 'row' }}>
            <Button style={{}} type='outline' title='100%' onPress={
              () => this.setState({ amountInput: staked })
            } />
            <View style={{ width: vp }} />
            <View style={{ flex: 1 }}>
              <Button disabled={!enableUnstake} title={'Unstake'} onPress={
                () => this.doUnstake(this.state.amountInput)} />
            </View>
          </View>
          <Text> </Text>
          <Text>{msgReward}</Text>
          <Text> </Text>
          {btnClaimReward}
          {btnUpdate}
          {btnBack}
        </View>
      </View>
    );
  }

}

export default Farm;
