async function cmfFarmGlobalData() {
  const provider = RO_PROVIDER;
  const tokenFarm = RO_TOKENFARM;
  const token = RO_TOKEN;

  const block = await provider.getBlock();
  const ts = block.timestamp;

  // apr token farm
  const apr = await calcAprTokenFarm();

  return {
    ts,
    apr
  };
}

async function cmfFarmUserData(userAddress) {
  const token = RO_TOKEN;
  const tokenFarm = RO_TOKENFARM;

  const owned = await token.balanceOf(userAddress);
  const approved = await token.allowance(userAddress, TOKENFARM_ADDRESS);
  const ud = await tokenFarm.getUserData(userAddress);
  const staked = ud[1];
  const pendingWithdraw = ud[4];
  const unstakeTimestamp = ud[2].toNumber();
  const withdrawTimestamp = ud[3].toNumber();

  const reward = await tokenFarm.getUnclaimedReward(userAddress);
  const claimTimestamp = (await tokenFarm.nextUpdateTime()).toNumber();

  // debug only
  const tokenFarmWallet = await tokenFarm.ADDRESS_WALLET();
  const tokenFarmWalletToken = await token.balanceOf(tokenFarmWallet);
  const extraData = {
    tokenFarmWallet,
    tokenFarmWalletToken: wei2eth(tokenFarmWalletToken)
  }
  log.debug('** tokenfarm **');
  log.debug(extraData);

  return {
    owned: wei2eth(owned),
    approved: wei2eth(approved),
    staked: wei2eth(staked),
    reward: wei2eth(reward),
    pendingWithdraw: wei2eth(pendingWithdraw),
    unstakeTimestamp,
    claimTimestamp,
    withdrawTimestamp
  };
}

async function cmfFarmApprove() {
  const tx = await TOKEN.approve(TOKENFARM_ADDRESS, maxUINT);
  await tx.wait();
  return tx.hash;
}

async function cmfFarmStake(amount) {
  const tx = await TOKENFARM.stakeToken(eth2wei(amount));
  await tx.wait();
  return tx.hash;
}

async function cmfFarmUnstake(amount) {
  const tx = await TOKENFARM.unstakeToken(eth2wei(amount));
  await tx.wait();
  return tx.hash;
}

async function cmfFarmClaim() {
  const tx = await TOKENFARM.claimReward();
  await tx.wait();
  return tx.hash;
}

async function cmfFarmWithdraw() {
  const tx = await TOKENFARM.withdrawToken();
  await tx.wait();
  return tx.hash;
}

async function cmfFarmUpdate() {
  const tx = await TOKENFARM.updateTurn();
  await tx.wait();
  return tx.hash;
}

